.social-icon {
    width: 40px;
    height: 40px;
    border-radius: 50%;
    overflow: hidden;
    -webkit-transition: all .2s ease-in-out;
    transition: all .2s ease-in-out;
    margin-right: 10px;
    border: 1px solid white;
}

.social-icon-sm {
    width: 32px;
    height: 32px;   
}

.social-icon-lg {
    width: 80px;
    height: 80px;
}

.social-icon:last-child {
    margin-right: 0px;
}

.social-icon:hover {
    -webkit-transform: scale(.95,.95);
            transform: scale(.95,.95);
    box-shadow: .5px .5px 2px lightgray;
}

.social-icon:active,
.social-icon:focus {
    -webkit-transform: scale(.95,.95) translateX(1px) translateY(1px);
            transform: scale(.95,.95) translateX(1px) translateY(1px);
}

.social-icon img {
    width: 100% !important;
    height: 100% !important;
}

.social-icons-panel {
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    align-items: flex-end;  
}

.social-icons-panel .social-phone {
    margin: 0;
    padding: 0;
    color: white;
    font-weight: bold;
    font-size: .85rem;
    line-height: 1rem;
}

.social-icons-panel .social-phone:hover {
    color: orange;
    text-decoration: none;
}

.inner-border {
    border: 1.5px solid white;
    box-shadow: inset 0px 0px 0px 1px white;
    box-sizing: border-box;
}

/* Responsive */
@media (max-width: 1024px) {
    .social-icons-panel {
        padding: 8px 20px;
    }
}

@media (max-width: 575px) {
    .social-icons-panel {
        padding: 0;
    }

    .social-icon {
        width: 32px;
        height: 32px;
    }
}
/* Styles */
.navbar {
    position: fixed !important;
    top: 0;
    left: 0;
    width: 100vw;
    background-color: black;
    color: white;
    font-size: 1rem;
    margin: 0;
    padding: 10px !important;
    border-bottom: 2px solid orange;
    z-index: 5;
    flex-flow: column wrap !important;
    -webkit-transition: all .3s ease-in;
    transition: all .3s ease-in;
    -webkit-transform: translateY(0);
            transform: translateY(0);
}

.navbar.navbar--reduced {
    border: none;
}

.navbar.navbar--hide {
    -webkit-transform: translateY(-100%);
            transform: translateY(-100%);
}

.navbarsocial {
    height: 100%;
    -webkit-transition: opacity .5s, all .2s;
    transition: opacity .5s, all .2s;
}

.navbarsocial.navbarsocial--hide {
    opacity: 0;
    -webkit-transform: translateY(200%);
            transform: translateY(200%);
    display: none;
}

.hide {
    position: absolute !important;
    top: -9999px !important;
    left: -9999px !important;
 }
 
.navbar > .container {
    flex-flow: column nowrap !important;
}

.navbar.navbar--reduced > .container {
    flex-flow: row wrap !important;
}

.navbar > .container > .navbar--first {
    width: 100%;
    display: flex;
    flex-flow: row nowrap !important;
    justify-content: space-between !important;
}

.navbar > .container > .navbar--last {
    width: 100%;
    display: flex;
    flex-flow: row nowrap !important;
    align-content: space-around !important;
}

.navbar.navbar--reduced > .container > .navbar--first,
.navbar.navbar--reduced > .container > .navbar--last {
    width: auto;
}

.navbar-logo {
    padding: 0;
    width: 100%;
    -webkit-transition: all .4s ease-in;
    transition: all .4s ease-in;
}

.navbar.navbar--reduced .navbar-logo {
    width: 350px;
}

.navbar .navbar-brand {
    padding: 0;
    margin: 0;
    font-size: 0;
    line-height: 0;
}

.navbar .navbar-nav .nav-item a {
    padding: 8px 20px;
    padding-bottom: 0;
    margin: 0;
    font-weight: 700;
    letter-spacing: 2px;
    text-transform: uppercase;
    color: white;
}

.navbar .navbar-nav .nav-item a:last-child {
    padding-right: 0;
}

.navbar .navbar-nav .nav-item a:active,
.navbar .navbar-nav .nav-item a:hover, 
.navbar .navbar-nav .nav-item a:focus {
    color: #ffb400;
}

/* Animations */

/* Responsive */
@media (max-width: 991px) {
    .navbar .container .navbar--first {
        width: 100% !important;
    }
}

@media (max-width: 425px) {
    .navbar-logo,
    .navbar.navbar--reduced .navbar-logo {
        width: 275px !important;
    }

    .navbar-toggler {
        padding: .15rem .50rem !important;
        font-size: .9rem !important;
        line-height: .9rem !important;
    }
}
.hero {
    background-color: black;
    display: grid;
    text-align: center;
    top: 0;
    overflow: hidden;
    position: absolute;
    height: 100%;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.hero-content {
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    display: flex;  
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    padding-top: var(--var-height-navbar-xl);
}

.hero-content h1 {
    text-transform: uppercase;
    font-weight: 400;
    font-size: 3rem;
    color: white;
}

.hero-content h1 span {
    font-weight: 700;
}

.hero-content h1 span:last-child {
    color: #ffb400;
}

.hero-img {
    height: 100vh;  
    width: 100%;
    object-fit: cover;
}

.hero-panel-text {
    background-color: rgba(192, 166, 166, 0.2);
    padding: 35px 50px;
    margin-top: 50px;
    max-width: 1024px;
    text-align: center;
    color: white;
}

.hero-panel-text h2 {
    font-size: 2rem;
    margin-bottom: 1rem;
}

.hero-panel-text p {
    position: relative;
    padding-top: 25px;
    padding-left: 80px;
    text-align: left;
}

.hero-panel-text p cite {
    font-size: 1.5rem;
}

.hero-panel-text p cite::before {
    position: absolute;
    content: '\275D';
    display: block;
    top: -2.5rem;
    left: -2rem;
    font-size: 10rem;
    color: #ffb400;
}

.hero-buttons {
    margin-top: 100px;
}

.hero-buttons a:first-child {
    margin-right: 50px;
}

.logo-coches-net { 
    display: inline-block;
    margin-top: 10px;
    margin-left: 10px;
    margin-bottom: 5px;
    width: 11rem;
    background-color: white;
    border-radius: 5px;
    padding: 10px;
    border: 1px solid white;
    -webkit-transition: all .1s ease-in;
    transition: all .1s ease-in;
}

.logo-coches-net:hover {
    border: 1px solid orange;
    box-shadow: 3px 3px 6px white;
}

.logo-coches-net:active {
    box-shadow: 1px 2px 6px white;
    -webkit-transform: translateX(1px) translateY(1px);
            transform: translateX(1px) translateY(1px);
}

/* Responsive */
@media (max-width: 823px) {   
    .hero-content h1 {
        font-size: 1.8rem;
    }

    .hero-panel-text {
        background-color: transparent;
        margin: 0;
        padding: 0;
    }

    .hero-buttons {
        width: 100%;
        margin-top: 10px;
        padding: 15px;
    }

    .hero-buttons a:first-child {
        margin-right: 10px;
        margin-bottom: 10px;
    }
}

@media (max-width: 500px) {
