.main-title {
    text-align: center;
    margin: 0 auto;
    margin-bottom: 25px;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.main-title-h1 {
    font-size: 30px;
    text-transform: uppercase;
    max-width: 700px;
    color: #333;
    font-weight: 700;
    position: relative;
    line-height: 30px;
    padding: 0 0 20px 0;
    margin: 0 0 35px;
    text-align: center;
    border-bottom: 5px solid orange;
}

.main-title-p {
    font-weight: 300;
    font-size: 1.2rem;
    word-spacing: 3px;
    line-height: 30px;
    color: #808080;
}